import { useState } from "react";
import GuardarProducto from "./GuardarProducto";
import MostrarProductos from "./MostrarProductos";
import Footer from "./Footer";

function Home() {
    const [usuario, setUsuario] = useState("");
    const [contrasena, setContrasena] = useState("");
    const [isAle, setIsAle] = useState(false);
    const [error, setError] = useState("");
    const [showLogin, setShowLogin] = useState(false); // Estado para controlar la visibilidad del formulario de login

    const handleLogin = () => {
        if (usuario === "Ale" && contrasena === "andu1234") {
            setIsAle(true);
            setError(""); // Limpiar errores si la autenticacin es correcta
        } else {
            setIsAle(false);
            setError("Usuario o contrasea incorrectos");
        }
    };

    return (
        <div className="displayFlex">
            {/* Mostrar formulario de login solo si showLogin es true */}
            {showLogin && !isAle && (
                <div className="vertical">
                    <h3>Iniciar sesin</h3>
                    <input
                        className="inputLog"
                        type="text"
                        placeholder="Nombre de usuario"
                        value={usuario}
                        onChange={(e) => setUsuario(e.target.value)}
                    />
                    <input
                        className="inputLog"
                        type="password"
                        placeholder="Contrasea"
                        value={contrasena}
                        onChange={(e) => setContrasena(e.target.value)}
                    />
                    <button className="login" onClick={handleLogin}>
                        Entrar
                    </button>
                    {error && <p style={{ color: "red" }}>{error}</p>}
                </div>
            )}

            {/* Si el usuario ya est logueado, muestra el contenido protegido */}
            {isAle && (
                <div>
                    <h2>Bienvenido, {usuario}</h2>
                    <GuardarProducto />
                </div>
            )}

            {/* Contenido pblico visible para todos */}
            <div>
                <MostrarProductos />
                <Footer setShowLogin={setShowLogin} />
            </div>
        </div>
    );
}

export default Home;
