import React from 'react';
import whats from './logos/what.webp';
import insta from './logos/insta.png';
import contact from './logos/contact.png';

const Footer = ({ setShowLogin }) => {
    return (
        <div className='imageFooter'>
            {/* Enlace a WhatsApp */}
            <a href="https://wa.me/+5492944211270" target="_blank" rel="noopener noreferrer">
                <img
                    className='imageFooter2'
                    alt='whatsapp'
                    src={whats}
                    title="Contctanos por WhatsApp!"
                />
            </a>

            {/* Enlace a Instagram */}
            <a href="https://www.instagram.com/huma.brc" target="_blank" rel="noopener noreferrer">
                <img
                    className='imageFooter2'
                    alt='instagram'
                    src={insta}
                    title="Sguenos en Instagram"
                />
            </a>

            {/* Enlace a la pgina de contacto */}
            <a href="/contact" target="_blank" rel="noopener noreferrer">
                <img
                    className='imageFooter2'
                    alt='contacto'
                    src={contact}
                    title="Contctanos"
                />
            </a>

            {/* Botn para mostrar el login */}
            <button
                onClick={() => setShowLogin((prev) => !prev)}
                style={{ marginTop: '10px', padding: '5px 15px', backgroundColor: '#4CAF50', color: 'white', border: 'none', borderRadius: '5px', cursor: 'pointer' }}
            >
                {setShowLogin ? "Abrir Login" : "Iniciar Sesin"}
            </button>
        </div>
    );
};

export default Footer;
