const Cart = ({ cartItems, setCartItems }) => {
    if (!Array.isArray(cartItems)) {
        cartItems = [];
    }

    const increaseQuantity = (index) => {
        const newCart = [...cartItems];
        newCart[index].cantidad += 1;
        setCartItems(newCart);
    };

    const decreaseQuantity = (index) => {
        const newCart = [...cartItems];
        if (newCart[index].cantidad > 1) {
            newCart[index].cantidad -= 1;
        } else {
            newCart.splice(index, 1);
        }
        setCartItems(newCart);
    };

    const getTotal = () => {
        return cartItems.reduce((total, item) => total + item.precio * item.cantidad, 0);
    };

    const clearCart = () => {
        setCartItems([]);
    };

    const handleCheckout = () => {
        // Aqu puedes agregar lgica para procesar el pago o redirigir al usuario a una pgina de pago
        alert("Compra realizada con xito! Total: $" + getTotal());
        clearCart(); // Opcionalmente, vaciar el carrito despus de la compra
    };

    return (
        <div style={{ padding: "4px", width: "120px", backgroundColor: "#f9f9f9", }}>
            <h2> </h2>
            {cartItems.length === 0 ? (
                <p>VACIO</p>
            ) : (
                <>
                    {cartItems.map((item, index) => (
                        <div key={index} style={{ borderBottom: "1px solid #5c2", padding: "10px" }}>
                            <h4>{item.nombre}</h4>
                            <p>Precio: ${item.precio}</p>
                            <p>Cantidad: {item.cantidad}</p>
                            <button className="masOmenos" onClick={() => decreaseQuantity(index)}>-</button>
                            <button className="masOmenos" onClick={() => increaseQuantity(index)}>+</button>
                        </div>
                    ))}
                    <h3>Total: ${getTotal()}</h3>
                        <button onClick={clearCart} style={{ backgroundColor: "#d21", color: "white", padding: "6px", borderRadius: "5px", border: "none", cursor: "pointer" }}>
                        VACIAR 
                    </button>
                    {/* Botn de comprar */}
                    <button onClick={handleCheckout} style={{ backgroundColor: "green", color: "white", padding: "6px", borderRadius: "5px", border: "none", cursor: "pointer", marginTop: "10px" }}>
                        COMPRAR 
                    </button>
                </>
            )}
        </div>
    );
};

export default Cart;
